'use server';

/**
 * @fileOverview This file defines a Genkit flow for providing personalized internship recommendations to students.
 *
 * It uses the student's profile and skills to suggest relevant internship opportunities.
 *
 * @interface PersonalizedInternshipRecommendationsInput - Defines the input schema for the flow.
 * @interface PersonalizedInternshipRecommendationsOutput - Defines the output schema for the flow.
 * @function personalizedInternshipRecommendations - The main function that triggers the flow.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const PersonalizedInternshipRecommendationsInputSchema = z.object({
  profile: z.string().describe('The student profile, including skills, experience, and education.'),
  applicationHistory: z.string().describe('The student past application history.'),
  interests: z.string().describe('The student specified interests.'),
});

export type PersonalizedInternshipRecommendationsInput = z.infer<
  typeof PersonalizedInternshipRecommendationsInputSchema
>;

const PersonalizedInternshipRecommendationsOutputSchema = z.object({
  recommendations: z
    .string()
    .describe('A list of internship recommendations based on the student profile and skills.'),
});

export type PersonalizedInternshipRecommendationsOutput = z.infer<
  typeof PersonalizedInternshipRecommendationsOutputSchema
>;

export async function personalizedInternshipRecommendations(
  input: PersonalizedInternshipRecommendationsInput
): Promise<PersonalizedInternshipRecommendationsOutput> {
  return personalizedInternshipRecommendationsFlow(input);
}

const prompt = ai.definePrompt({
  name: 'personalizedInternshipRecommendationsPrompt',
  input: {schema: PersonalizedInternshipRecommendationsInputSchema},
  output: {schema: PersonalizedInternshipRecommendationsOutputSchema},
  prompt: `You are an AI assistant designed to provide personalized internship recommendations to students.

  Based on the student's profile, application history and interests, suggest the best internship opportunities.

  Student Profile: {{{profile}}}
  Application History: {{{applicationHistory}}}
  Interests: {{{interests}}}

  Provide a detailed list of internship recommendations that align with the student's qualifications and preferences.
  `,
});

const personalizedInternshipRecommendationsFlow = ai.defineFlow(
  {
    name: 'personalizedInternshipRecommendationsFlow',
    inputSchema: PersonalizedInternshipRecommendationsInputSchema,
    outputSchema: PersonalizedInternshipRecommendationsOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
